@import url("colors.css");

.footerContentHolder {
  position: relative;
  max-width: 1090px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

.footer {
  margin-top: 165px;
  padding-bottom: 46px;
  background-color: var(--offBlack);
  color: var(--white);
  width: 100vw;
  position: relative;
  left: calc(-50vw + 50%);
}

.nav {
  padding-top: 35px;
  padding-bottom: 35px;
  color: var(--white);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav a {
  text-decoration: none;
  color: var(--white);
}

.nav a:hover {
  color: var(--orange);
}

.logo {
  font-size: 24px;
  font-weight: 700;
}

.navLinks {
  display: flex;
  gap: 34px;
  /* gap: 3%; */
  font-size: 13px;
  font-style: normal;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.footerBottom {
  margin-top: 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footerTextContent {
  max-width: 520px;
  color: rgb(158, 154, 154);
  font-size: 0.9rem;
  font-style: normal;
  font-weight: 500;
  line-height: 166.667%;
  text-align: left;
}

.footerTextContentCopyright {
  margin-top: 56px;
}

.footerSocialLinks {
  display: flex;
  gap: 16px;
}

hr {
  width: 100px;
  height: 4px;
  background-color: var(--darkOrange);
  border: inherit;
  position: absolute;
  top: -8px;
  left: 0;
}

@media (max-width: 768px) {
  .footer {
    margin-top: 96px;
  }

  .nav {
    flex-direction: column;
    align-items: start;
    gap: 32px;
  }

  .footerBottom {
    margin-top: 0px;
  }

  .footerSocialLinks {
    position: relative;
    top: 90px;
  }

  .footerSocialLinks a {
    width: 24px;
  }
}

@media (max-width: 480px) {
  .nav {
    align-items: center;
  }
  .navLinks {
    flex-direction: column;
    gap: 16px;
  }

  .footerBottom {
    flex-direction: column;
    align-items: center;
  }

  .footerTextContent {
    text-align: center;
  }

  .footerSocialLinks {
    top: 28px;
  }
}
